import Table from "react-bootstrap/Table";
import { useDispatch, useSelector } from "react-redux";
import { changeAge } from "../store/userSlice";
import {
  decreaseAmount,
  increaseAmount,
  deleteItem,
} from "../store/cartData.js";

function Cart() {
  // Redux store 가져와주는 Hook.
  // Redux store 에 있던 모든 state가 남음.
  let user = useSelector((state) => state.user);

  // useDipatch 는 store.js로 요청 보내주는 React hook.
  let dispatch = useDispatch();

  let cartData = useSelector((state) => state.cartData);

  return (
    <>
      <div>
        <h6>
          {user.name} {user.age}의 장바구니
        </h6>
        <button
          onClick={() => {
            dispatch(changeAge(1));
          }}
        >
          나이 + 1
        </button>
        <Table>
          <thead>
            <tr>
              <th>제품 ID</th>
              <th>상품명</th>
              <th>수량</th>
              <th>변경하기</th>
              <th>삭제</th>
            </tr>
          </thead>
          <tbody>
            {cartData.map((v, i, a) => {
              return (
                <tr key={i}>
                  <td>{cartData[i].id}</td>
                  <td>{cartData[i].name}</td>
                  <td>{cartData[i].count}</td>
                  <td>
                    <button
                      style={{
                        marginRight: "10px",
                        paddingRight: "8px",
                        paddingLeft: "8px",
                        borderStyle: "none",
                      }}
                      onClick={() => {
                        // cartData[i].id --> 버튼 옆에있는 ID
                        dispatch(decreaseAmount(cartData[i].id));
                      }}
                    >
                      -
                    </button>

                    <button
                      style={{ borderStyle: "none" }}
                      onClick={() => {
                        dispatch(increaseAmount(cartData[i].id));
                      }}
                    >
                      +
                    </button>
                  </td>
                  <td>
                    <button
                      style={{ borderStyle: "none" }}
                      onClick={() => {
                        dispatch(deleteItem(cartData[i].id));
                      }}
                    >
                      삭제
                    </button>
                  </td>
                </tr>
              );
            })}
            <tr></tr>
          </tbody>
        </Table>
      </div>
    </>
  );
}

export default Cart;
